@use "./../mixins";

.hero {
  padding: 90px 19px 85px;
  background-image:
    url("../img/index/hero-divider-desk.svg"),
    url("../img/index/banner-desk.jpg"),
    linear-gradient(0deg, rgba(129, 179, 210, 1) 0%, rgba(255, 255, 255, 1) 100%);
  background-repeat: no-repeat, no-repeat;
  background-position: center calc(100% + 0.5px), center -40px;
  background-size: auto, auto;

  @include mixins.tablet {
    padding: 77px 60px 91px;
    background-position: center calc(100% + 0.5px), center center;
    background-size: auto auto, cover;
  }

  @include mixins.desktop {
    padding: 78px 120px 90px;
  }

  &--size-small {
    padding: 52px 19px 67px;
    background-image:
      url("../img/index/hero-divider-desk.svg"),
      url("../img/catalog/banner-small-desk.jpg"),
      linear-gradient(0deg, rgba(129, 179, 210, 1) 0%, rgba(255, 255, 255, 1) 100%);
    background-position: center calc(100% + 0.5px), -390px -170px;

    @include mixins.tablet {
      padding: 95px 60px 117px;
      background-position: center calc(100% + 0.5px), center bottom;
      background-size: auto auto, auto;
    }

    @include mixins.desktop {
      padding: 101px 120px 120px;
    }
  }
}

.hero__image {
  display: block;
  max-width: 100%;
  margin: 0 auto;
  object-fit: contain;

  @include mixins.tablet {
    width: 460px;
    height: auto;
  }
}
